
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class LoginService {

  baseUrl = environment.baseUrl;
  
  public loginStatusSubject = new Subject<boolean>();
  clearTimeout:any;

  constructor(private http: HttpClient,private toastr: ToastrService,) {}

  //current user : which is loogedin
  public getCurrentUser() {
    return this.http.get(`${this.baseUrl}/currentUser`);
    
  }


  //generate Token
  public generateToken(loginData: any) {
    return this.http.post(`${this.baseUrl}/generateToken`, loginData);
  }

  //login user : set token in local storage
  public loginUser(token) {
    localStorage.setItem('token', token);
    // this.loginStatusSubject.next(true);
    return true;
  }

  //isLoogedIn: user is loggedin or not
  public isLoggedIn() {
    let tokenStr = localStorage.getItem('token');
    if (tokenStr == undefined || tokenStr == '' || tokenStr == null) {
      return false;
    } else {
      return true;
    }
  }

  //logout:: remove token from localstorage
  public logout() {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    localStorage.removeItem('activeTabName');
    if(this.clearTimeout){
      clearTimeout(this.clearTimeout);
    }
    this.toastr.warning('Goodbye','logout');
    return true;
  }

  //getToken
  public getToken() {
    return localStorage.getItem('token');
  }

  //set userDetail
  public setUser(user) {
    localStorage.setItem('user', JSON.stringify(user));
  }


  //getUser
  public getUser() {

    let userStr = localStorage.getItem('user');
    if (userStr != null) {
      return JSON.parse(userStr);
    } else {
      this.logout();
      return null;
    }
    
  }


  //get user role
  // public getUserRole() {
  //   let user = this.getUser();
  //   return user.authorities[0].authority;
  // }

  
  //get superadmin role
  public getSuperAdminRole() {
    let superAdmin = this.getUser();

    function findSuperAdmin(roleSuperAdmin) {
      return roleSuperAdmin.authority === 'ROLE_SUPER_ADMIN';
    }
    return superAdmin.authorities.find(findSuperAdmin);
  }

  // get admin role
  public getAdminRole() {
    let admin = this.getUser();

    function findAdmin(roleAdmin) {
      console.warn(roleAdmin.authority)
      return roleAdmin.authority=== 'ROLE_ADMIN';
    }
    // console.warn(admin.authorities.find(findAdmin).authority);
    return admin.authorities.find(findAdmin);
  }

 //get user role
 public getUserRole(){
console.log("OK COMMING.....");

  let hrCremp=this.getUser();
  console.log("ROLE"+hrCremp.authorities[0].authority);
  
  // return hrCremp.authorities[0].authority;
  //return user.authorities;

  return hrCremp.authorities[0].roleName;
}


  // Register User
  public register(user){
    return this.http.post(`${this.baseUrl}/user/register`,user);
  }
  // Get By Id 
  public getById(id){
    return this.http.get(`${this.baseUrl}/user/get/${id}`);
  }


}
